// General woocommerce that doesn't belong anywhere else

.woocommerce{
  position: relative;
  .woocommerce-cart &{
    padding-top: $padding-deep;
    padding-bottom: $padding-deep;
  }
}

@mixin variationtable{
  /* stylelint-disable */
  display: grid;
  padding: 0 !important;
  line-height: 1.2 !important;
  grid-template-columns:40% 1fr;
  gap:0.5em 1em;
  dt, dd, p{
    margin: 0 !important;
    padding: 0 !important;
    font-family: $font-primary;
    font-size:14px !important;
    font-weight: 100 !important;
    line-height: 1.2 !important;
  }
  dt, dd{
    @include breakpoint($break-desktop) {
      display: block;  
    }
  }
  dt{
    padding-right: 0.5em;
  }
  /* stylelint-enable */
}



.woocommerce-NoticeGroup{
  @include notice-inline;
}

address, address p{
  @include text-regular;
}
address p{
  margin-top: 0.5em;
}

.form-row{
  margin-bottom: 1.5rem;
}

/* stylelint-disable */
.crafty_billing{
  label{
    height: 2em;
  }
  &*{
    float:none !important;
  }
  &.form-row{
    &-first{
      flex-grow: 1;
      width: unset !important;
    }
    &-last{
      width: unset !important;
    }
  }
}

.crafty_billing.address-field{
  button{
    position: relative;
    top: -2.5rem;
  }
}
#billing_cp_button{
  top: unset !important;
  float: none !important;
  font-size:1.6rem !important;
}
#billing_country{
  padding-bottom: 1.5rem;
}

// country selector

.select2{
  &-selection{
    height: auto !important;
    border: $borderline-darker !important;
    border-radius: 0 !important;
    font-size: 1.6rem !important;
  }
  &-container{
  }
  &-dropdown{
    border: $borderline-darker !important;
  }
  &-search{
    padding: 0.6em !important;
    font-size: 1.6rem !important;
    &__field{
      border: $borderline-darker !important;
    }
  }
  &-results{
    &__options{
      font-size: 1.6rem !important;
      li{
        padding: 0.6em !important;
      }
    }
    &__option--highlighted{
      background-color: $brand-ocean !important;
    }
  }
}

/* stylelint-enable */
